import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { UsersWidgetModule } from './_widgets/users.widget';
import { PersonsWidgetModule } from '../+persons/_widgets/persons.widget';
import { SharedModule } from '../shared/shared.module';

import { UsersComponent } from './users/users.component';
import { UserAddComponent } from './user-add/user-add.component';
import { UserUpdateComponent } from './user-update/user-update.component';

import { UsersService } from './users.service';

const USERS_ROUTES: Routes = [
  { path: '', component: UsersComponent },
  { path: 'add', component: UserAddComponent },
  { path: 'update/:id', component: UserUpdateComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(USERS_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    UsersWidgetModule,
    PersonsWidgetModule,
    SharedModule
  ],
  declarations: [UsersComponent, UserAddComponent, UserUpdateComponent],
  providers: [UsersService],
  exports: [UsersComponent]
})
export class UsersModule { }



