import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { User } from '../user';
import { UsersService } from '../users.service';

import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';
import { PaginationService } from '../../shared/pagination.service';
import { MaterializeAction } from 'angular2-materialize/src';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {
  public users: User[];
  public currentItem: User;
  public modalDeleteActions = new EventEmitter<string|MaterializeAction>();

  constructor(
    private _usersService: UsersService,
    private _toastService: ToastService,
    private _toolbar: ToolbarService,
    private _pager: PaginationService,
    private _router: Router
  ) { }

  ngOnInit() {
    this._pager.clear();

    this._toolbar.change({
      title: "Список пользователей",
      name: "users",
      actions: {
        add: () => this._router.navigate(['users/add'])
      },
      actionsPosition: {
        extend: ['add'] 
      }
    });
      
    this._usersService.get()
      .subscribe((data) => {
        this.users = data;
      });
  }

  openModal(item) {
    this.currentItem = item;
    this.modalDeleteActions.emit({action:"modal",params:['open']});
  }

  closeModal() {
    this.modalDeleteActions.emit({action:"modal",params:['close']});
  }

  deleteItem(item) {
    let self = this;

    this._usersService.remove(item._id)
      .subscribe((data: any) => {
        if(data.status == 'ok') {
          this.users.find((user, index) => {
            if(item._id == user._id) {
              self.currentItem = null;
              self.users.splice(index, 1);
              return true;
            }
          });
          this._toastService.toast("Пользователь удален");
        }
      });
  }

}



