import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { IUser, User, UserForm } from './user';

@Injectable()
export class UsersService {
  	private _baseUrl: string = "/api/v1/users";

  	constructor(private _http: Http) { }

  	get() : Observable<User[]> {
		return this._http.get(this._baseUrl)
			.map( (res: Response) => res.json() )
			.catch( (error: any) => Observable.throw(error.json().errors || "Server error"));
	}

	getOne(id: string) : Observable<UserForm> {
		return this._http.get(`${this._baseUrl}/${id}`)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
	}

	add(body: IUser) : Observable<IUser> {
		let bodyString = JSON.stringify(body);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.post(this._baseUrl, JSON.stringify(body), options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
	}

	update(body: IUser) : Observable<IUser> {
		let bodyString = JSON.stringify(body);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.put(`${this._baseUrl}/${body._id}`, JSON.stringify(body), options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
	}

	remove(id: string) : Observable<User> {
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});
		
		return this._http.delete(`${this._baseUrl}/${id}`)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
	}
}



