import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { UsersService } from '../users.service';

import { ToolsService } from '../../shared/tools.service';

import { User, UserForm } from '../user';

@Component({
  selector: 'app-user-update',
  templateUrl: './user-update.component.html',
  styleUrls: ['./user-update.component.css']
})
export class UserUpdateComponent implements OnInit {
  public form: FormGroup;
  public datePickerOptions: any;
  public getUser$: Observable<UserForm>;
  public user: UserForm = new UserForm();

  constructor(
    private _usersService: UsersService,
    private _tools: ToolsService,
    private _fb: FormBuilder,
    private _router: Router,
    private _currentRoute: ActivatedRoute
  ) { 
    this.form = this.createFormGroup();
  }

  ngOnInit() {
    this._tools.getToolbar().change({
      title: "Добавить пользователя"
    });

    this._currentRoute.params.forEach((params: Params) => {
      this.getUser$ = this._usersService.getOne(params['id']);
      this.getUser$.subscribe(
        (user) => {
          this.user.setParams(user);
          this.form.patchValue({user: user, person: user.person});
        }
      );
    });
  }

  matchingPasswords(passwordKey: string, passwordConfirmationKey: string): any {
    return (group: FormGroup) => {
      let passwordInput = group.controls[passwordKey];
      let passwordConfirmationInput = group.controls[passwordConfirmationKey];
      if (passwordInput.value !== passwordConfirmationInput.value) {
        return passwordConfirmationInput.setErrors({notEquivalent: true})
      }
    }
  }

  onSubmit() {
    if(this.checkValidForm()) {
      let user = this.createDataForSave();

      this._usersService.update(user)
        .subscribe(
          (data) => this._tools.getToast().toast("Пользователь успешно обновлен!"),
        );
    }
  }

  toList() {
    this._router.navigate(["/users"]);
  }

  private checkValidForm() {
    let validUser = this.form.controls['user'].valid;
    let validPerson = this.form.controls['person'].valid || !this.form.controls['person'].dirty;

    return (validPerson && validUser); 
  }

  private createDataForSave() {
    var user = new UserForm();
    user.setParams(this.form.controls['user'].value);
    user.password = (<FormGroup>this.form.controls['user']).controls['passwords'].value.password;

    if(this.form.controls['person'].dirty || this.form.controls['person'].valid) {
      user.person.setParams(this.form.controls['person'].value);
      user.personId = this.form.controls['person'].value._id || null;
    }

    return user;
  }

  private createFormGroup() {
    return this._fb.group({
      user: this._fb.group({
        _id: this._fb.control('', [<any>Validators.required]),
        username: this._fb.control('', [<any>Validators.required, <any>Validators.minLength(2)]),
        passwords: this._fb.group({
          password: this._fb.control('', [<any>Validators.required]),
          passwordAgain: this._fb.control('', [<any>Validators.required]),
        }, this.matchingPasswords('password', 'passwordAgain')),      
        group: this._fb.control(''),
        active: this._fb.control(''),
        personId: this._fb.control('')
      }),
      person: this._fb.group({
        _id: this._fb.control(''),
        first_name: this._fb.control('', [<any>Validators.required, <any>Validators.minLength(2)]),
        second_name: this._fb.control(''),
        last_name: this._fb.control('', [<any>Validators.required, <any>Validators.minLength(2)]),
        gender: this._fb.control(''),
        birthdate: this._fb.control(''),
        phone: this._fb.control('', [<any>Validators.required, <any>Validators.minLength(2)]),
        email: this._fb.control(''),
        picture: this._fb.control('')
      })
    });
  }
}



