import { IPerson, Person } from '../shared/person';

export interface IUser {
    _id?: string;
    username: string;
    active: boolean;
    resource: any[];
    group: string;
    personId: string;
}

export class User {
    _id?: string;
    public username: string;
    public createdAt: Date;
    public updatedAt: Date;
    public active: boolean;
    public resource: any[];
    public group: string;
    public personId: string;

    constructor(object?: any) {
        this.default();

        if(object != undefined) {
            this.setParams(object);
        }
    }

    clear(): void {
        this.default();
    }

    setParams(object: any) : void {
        this._id = object._id ? object._id : null;
        this.username = object.username ? object.username : null;
        this.setDate(this.createdAt, object.createdAt);
        this.setDate(this.updatedAt, object.updatedAt);
        this.active = object.active ? true : false; 
        this.resource = object.resource ? object.resource : [];
        this.group = object.group || this.group;
        this.personId = object.personId ? object.personId : null;
    }

    private setDate(self, date) {
        if(date) {
            if(date.search(/\d{2}.\d{2}.\d{4}/i) == -1) {
                self = date;
            } else {
                let [day, month, year] = date.split('.');
                self = new Date(year, month - 1, day, 0, 0, 0);
            }
        }
    }

    private default() : void {
        this._id = null;
		this.username = null;
        this.createdAt = null;
        this.updatedAt = null;
        this.active = true;
        this.resource = [];
        this.group = 'user';
        this.personId = null;
    }
}

export class UserForm implements IUser {
    _id?: string;
    public username: string;
    public password: string;
    public active: boolean;
    public resource: any[];
    public group: string;
    public personId: string;
    public person: Person;

    constructor(object?: any) {
        this.default();

        if(object != undefined) {
            this.setParams(object);
        }
    }

    clear(): void {
        this.default();
    }

    setParams(object: any) : void {
        this._id = object._id || this._id;
        this.username = object.username || this.username;
        this.password = object.password || this.password;
        this.resource = object.resource || this.resource;
        this.group = object.group || this.group;
        this.active = !!object.active;
        this.personId = object.personId || this.personId;
        this.person = new Person(object.person);
    }

    private default() : void {
        this._id = null;
		this.username = null;
        this.active = true;
        this.resource = [];
        this.personId = null;
        this.group = 'user';
        this.person = new Person();
    }
}


